.my-theme .navbar{
    background-color: #023047;
    font-size: 2.2rem;
}

.navbar a, .logo a {
    font-size: 4rem;
    color: #fff;
    letter-spacing: 2px;
    text-transform: uppercase;
}

.navbar a span, .logo a span{
    font-size: 2.5rem;
    font-weight: 400;
}

.my-theme .navbar a:hover{ 
    color: #ffb703;
}

.flex-centered {
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar .logo{
    width: 50px;
    height: 50px;
    border-radius: 100%;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    color:#023047;

}


.sidebar .user-info {
    background-color: #0093E9;
    background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);    
}

.sidebar .user-info .image {
    display: flex;
    align-items: center;
    justify-content: left;
}
.sidebar .user-info .name{
    font-weight: bold;
    font-size: 2.5rem;
    color: #023047;
    margin-left: 15px;
}
.sidebar .user-info .info-container .email, .sidebar .user-info .info-container .user-helper-dropdown {
    font-size: 1.5rem;
    color:#023047
}

.legal{
    background-color: #efefef;
}

.sidebar .legal .copyright{
    font-size: 15px;
}

.info-wrap .col-lg-3:nth-child(1) .info-box{
    background-color: #16697a !important;
}

.info-wrap .col-lg-3:nth-child(2) .info-box{
    background-color: #2a9d8f !important;
}

.info-wrap .col-lg-3:nth-child(3) .info-box{
    background-color: #118ab2 !important;
}

.info-wrap .col-lg-3:nth-child(4) .info-box{
    background-color: #e76f51 !important;
}

.login-page {
    background-image: url('https://1660200928.rsc.cdn77.org/wp-content/uploads/2019/07/How-to-Grow-Business-with-Steady-Sales-Flow-guest-post.jpg');
    background-position: cover;
    background-repeat: no-repeat;
    background-size: cover;
}

.login-box .card{
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
    border-radius: 10px;
    opacity: 0.95;
}

.login-box .card .input-group {
    border: 2px solid #023047;
    border-radius: 10px;
    overflow: hidden;
}

.login-box .card .input-group .input-group-addon{
    background-color: #023047;
    padding-left: 12px;
}

.login-box .card .input-group .input-group-addon .material-icons {
    color:#fff;
}

.login-box .card .input-group .form-line{
    display: unset;
}

.login-box .card .input-group .form-line input{
    padding: 5px 15px;
}

.login-box .col-xs-6, .login-box .col-xs-12{
    margin-bottom: 0;
}